$blue-color: #3299cc;

body {
    margin: 0;
    padding: 0;
    background-color: rgb(203, 205, 205);
    vertical-align: middle;
}

.app-wrapper {
    display: flex;
    justify-content: space-around;
    height: 100vh;
    align-items: center;
    .right-block {
        width: 49%;
        background-color: white;
        height: 98vh;
        overflow: auto;
    }
    .left-block {
        width: 49%;
        background-color: white;
        height: 98vh;
    }
}

.left-header {
    height: 48px;
    background-color: $blue-color;
    color: white;
    font-size: 20px;
    line-height: 48px;
    padding-left: 10px;
    display: flex;
    justify-content: space-between;
}

.css-1h9z7r5-MuiButtonBase-root-MuiTab-root.Mui-selected {
    color: $blue-color !important;
}

.css-1aquho2-MuiTabs-indicator {
    background-color: $blue-color !important;
}

.css-1q2h7u5.Mui-selected {
    color: $blue-color !important;
}

.css-ttwr4n {
    background-color: $blue-color !important;
}

.info-area {
    min-width: fit-content;
    width: 200px;
    border-bottom: 1px solid #eeeeee;
    margin-left: 10px;
    padding-bottom: 5px;
}

.patient-info-wrapper {
    display: flex;
    margin: 15px 10px;
}

tbody tr {
    &:hover {
        background-color: #eeeeee;
        cursor: pointer;
    }
    &.active {
        border-left: 2px solid $blue-color;
    }
}
.arrow {
    margin-top: 14px;
}

.vertical {
    width: 16px !important;
    opacity: 0.7;
}

.progress-wrapper {
    position: relative;
    width: 100%;
    height: 100%;

    span {
        width: 80px;
        height: 80px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
